import React from 'react';
import Row from './Row';

import ad1 from '../../assets/images/ad-1.jpg';

import fetchLinks from '../../FetchLinks';
import TwoRow from './TwoRow';
import Carousel from '../Header/Carousel';

function Home(props) {
    return (
        <div className="home">
            <Carousel fetchUrl={fetchLinks.headerCarouselFetchLink} />
            <Row
                links={[
                    {
                        linkTitle: '',
                        linkUrl: '/movies',
                        category: 'movie'
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/series',
                        category: 'series'
                    }
                ]}
                className="georgian"
                title=" "
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/"
            />

            <Row className="continue__watching" title=" " fetchUrl={fetchLinks.georgianDubFetchLink} forModalProps={props.forModalProps} />

            <div className="container-fluid p-0 home__ad__block">
                <img src={ad1} alt="" className="w-100" />
            </div>

            <TwoRow
                links={[
                    {
                        linkTitle: '',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="newly__added__series"
                title="  "
                fetchUrl={fetchLinks.newlyAddedSeriesFetchLink}
            />

            <Row
                className="newly__added__movies"
                title=" "
                fetchUrl={fetchLinks.newlyAddedMoviesFetchLink}
                seeMoreUrl="/"
            />

            <Row
                links={[
                    {
                        linkTitle: '',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="top__movies"
                title=" "
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/"
            />

            <Row
                links={[
                    {
                        linkTitle: '',
                        linkUrl: '/topByDay',
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByMonth'
                    },
                    {
                        linkTitle: '',
                        linkUrl: '/topByYear',
                    }
                ]}
                className="top__series"
                title=" "
                fetchUrl={fetchLinks.georgianDubFetchLink}
                seeMoreUrl="/" 
                />
                
            <Row
                className="trailers"
                title=""
                fetchUrl={fetchLinks.trailerFetchLink}
                seeMoreUrl="/trailers" 
                />


            <TwoRow
                className="collections"
                title=""
                fetchUrl={fetchLinks.collectionsFetchLink}
                seeMoreUrl="/collections"
            />


        </div>
    )
}

export default Home
